#weather h1 {
    font-family: 'Martel Sans', sans-serif;
    font-weight: 800;
    font-size: 3rem;
    margin-bottom: 0;
}

#weather p {
    font-family: 'Martel Sans', sans-serif;
    font-weight: 800;
    font-size: 1.5rem;
    text-align: center;
}
.World {
    background-image: url("https://images.unsplash.com/photo-1538980124103-c0fb4fd75e66?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=851&q=80");
    background-size: cover;
    background-position: center;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Martel Sans', sans-serif;
    font-weight: 800;
    font-size: 1.2rem;
}

input {
    border: none;
}

ul {
  list-style: none;
  margin: 0;
  height: 100%;
  width: auto;
}

.navbar {
  background-color: rgba(0, 0, 0, 0.7);
  padding: auto;
  height: 8%;
  position: absolute;
  left: 0px;
  top: 0px;
  right: 0px;
}

li {
  height: 100%;
  display: block;
  padding: 10px 0;
  height: 100%;
  padding-top: 17px;
  cursor: pointer;
  color: white;
  font-family: "Martel Sans", sans-serif;
  font-weight: 600;
  width: 45px;
}

li:hover {
  color: white;
}

